import React, {useState, useEffect} from 'react';
import Posts from './componentes/Posts';
import Pagination from './componentes/Pagination';
import SearchBox from './componentes/SearchBox';
import axios from 'axios';
import './App.css';

const App = () => {
  const [posts, setPosts] = useState([]);
  const [currentSearch, setCurrentSearch] = useState([]);
  const [loading, setLoading] = useState(false);
  const [currentPage, setCurrentPage] = useState(1);
  const [postsPerPage] = useState(10);
  const [search, setSearch] = useState("");


  useEffect(() => {
    const fetchPosts = async () => {
      setLoading(true);
      // si se accede como : http://localhost:3000/
      // const res = await axios.get('./ceptinel/data.json');

      // si se accede como : http://localhost:3000/ceptinel/
      var res = await axios.get('./data.json').then();

      setPosts(res.data.events);
      setLoading(false);
    }

    fetchPosts();
  }, []);

  // Get current posts
  const indexOfLastPost = currentPage * postsPerPage;
  const indexOfFirstPost = indexOfLastPost - postsPerPage;
  // const currentPost = posts.slice(indexOfFirstPost, indexOfLastPost);
  const searchPost = posts.filter((post) => {
      console.log(search.toUpperCase() + " " + post.eventBody.symbol);
      return post.eventBody.symbol.includes(search.toUpperCase()) || post.id == search;
  });


  const currentPost = searchPost.slice(indexOfFirstPost, indexOfLastPost);

  const vistos = posts.filter((post) => {
    return post.checked=="visto"
  }).length;

  const noVistos = posts.filter((post) => {
    return post.checked=="no visto"
  }).length;

  /*

  useEffect(() => {
    const searchPost = posts.filter((post) => {
      console.log(search.toUpperCase() + " " + post.eventBody.symbol);
      return post.eventBody.symbol.includes(search.toUpperCase())
    });

    //console.log(searchPost);
    currentSearch = setCurrentSearch(searchPost);

  })
  */
  // Cambio de página
  const paginate = pageNumber => setCurrentPage(pageNumber);

  const searching = search => setSearch(search);


  return (
    <div className='container mt-5'>
      <h1 className="text-primary mb-3">Ceptinel App</h1>
      <SearchBox searching={searching} />
      <Posts posts={currentPost} loading={loading} />
      <Pagination postsPerPage={postsPerPage} totalPosts={searchPost.length} paginate={paginate} />
      <div>Total: {posts.length } | Vistos: {vistos} | No vistos: {noVistos} | Filtrados: {currentPost.length} | Filtro: {search} </div>
    </div>
  );
};

export default App;
