import React from 'react'
import ReactDOM from 'react-dom';
import $ from 'jquery'
//import {jQuery as $} from './jquery';

const Posts = ({ posts, loading }) => {
  if (loading) {
    return <h2>Loading ...</h2>;
  }

  function handleToggle(name) {
    alert('Llamada aún no implementada para modificación en BackEnd ... ['  + name + ']');
  }

  return <table className="table">
    <tbody>
    {posts.map(post => (
      <tr key={post.id}>
        <td>{post.id}</td>
        <td>{post.eventBody.symbol}</td>
        <td>{post.labels}</td>
        <td>{post.timestamp}</td>
        <td>{post.status}</td>
        <td>{post.checked}</td>
        <td><button onClick={() => handleToggle(post.eventBody.symbol)}>
          {post.checked == 'no visto' ? 'no visto' : 'visto'}
        </button></td>
      </tr>
    ))}
    </tbody>
    </table>;
};

export default Posts;
